import { Component, ViewChild, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { Storage } from '@ionic/storage';
 

import { ImageViewerController } from 'ionic-img-viewer';
@IonicPage()
@Component({
  selector: 'page-badge-me-profile',
  templateUrl: 'badge-me-profile.html',
})
export class BadgeMeProfilePage {
  _imageViewerCtrl: ImageViewerController;
badgeImages:any;
badgeList:any;
ttlRecoginition:any;
userName:any;
client_id:any;
employeeId:any;
showData=false;
userImage:any;
loading:any;
// syncVal="0";
value: any=0;
user:any;
loadingval:any;
  deviceId: any;
  previousePageData:any;
  login_token: any;
  myinfinte:any;
  gotTotalBadges:any;
  badge:any=[];
  erroMsg: any;
  first_name: any='';
  constructor(public navCtrl: NavController,private zone:NgZone,
              public navParams: NavParams, imageViewerCtrl: ImageViewerController,
              private apiServices:ApiServiceProvider,
              private storage:Storage,
              private loadingCtrl:LoadingController ) {
              this.previousePageData= this.navParams.get('data');
              console.log('previouse page data==', this.previousePageData);
              this._imageViewerCtrl = imageViewerCtrl;
              this.userName=this.previousePageData.data.badge_user_name;
              console.log('previouse page data userName==', this.userName)
              this.userImage=this.previousePageData.data.badge_user_image;
              console.log('previouse page data userImage==', this.userImage);
              this.gotTotalBadges=this.previousePageData.data.count;
              console.log('previouse page data gotTotalBadges==', this.gotTotalBadges);
              this.loadData();

              this.storage.get('first_name').then((data) =>{this.first_name=data});
              

      }


  loadData(){
    // this.commonloader();
              this.storage.get('first_name').then((data) =>{this.first_name=data});
    this.storage.get('employeeId').then((data) =>{this.employeeId=data});
    this.storage.get('login_token').then((data) => {
      console.log('showOnBoard value is111==', data);
      this.login_token = data;
    })
    this.storage.get('deviceId').then(data => {
      this.deviceId = data;
      let apiKey = {
        "client_id":this.apiServices.clientId,
        "employee_id":this.employeeId,
        "device":this.apiServices.device,
        "device_id":this.deviceId,
        "app_version":this.apiServices.appVersion,
        "val":this.value,
        "user_type":this.previousePageData.badgeType,
        "user_id":this.previousePageData.data.badge_user_id
      };
      this.apiServices.badgeBoardApi(apiKey, this.login_token)
        .subscribe((res) => {
          console.log('badge profile List data==', res);
          // this.loading.dismiss();
          if(res.success==1){
              // this.userImage=res.user_image;
              if(this.badgeImages==undefined){
                this.badge=res.data;
                this.badgeImages=res.data;
                console.log('badge ==', this.badge);
              }else{
                this.zone.run(() => {
                  console.log('old array == ', this.badgeImages);
                  console.log('new data comming == ', res.data);
                  this.badgeImages= this.badgeImages.concat(res.data);
                // for(let i=0; i<res.data; i++){
                //   this.badgeImages.push(res.data[i]);
                //   if(i==res.data.length-1){
                //     this.myinfinte.complete();
                //   }
                // }
                this.myinfinte.complete();
                console.log('new array data== ', this.badgeImages);
                this.erroMsg='';
              });

              }

             
          }else{
            this.myinfinte.complete();
            this.erroMsg=res.message;
          }
      },(err) =>{

        })
      })  
    }

    doInfinite(infiniteScroll){
        setTimeout(() => {
          this.value=this.badgeImages.length;
          this.myinfinte=infiniteScroll;
          this.loadData();
      }, 1000);
    }

   
    imageToBeZoom(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();

    setTimeout(() => imageViewer.dismiss(), 30000);
    imageViewer.onDidDismiss(() => console.log('Viewer dismissed'));
  }

}



